@tailwind base;
@tailwind components;
@tailwind utilities;

html,
body,
#root {
  @apply h-full;
}
body {
  font-family: "Inter", sans-serif;
  font-optical-sizing: auto;
  @apply text-sm;
}
::backdrop {
  @apply backdrop-blur-sm;
}

.formik-field {
  @apply outline-none ring-1  px-2 py-1 rounded focus:ring-indigo-500 focus:ring-2 transition-colors;
}
